>1. +Á¨¶Âè∑ËøõË°åÂØπfoodÁöÑÊ∑ªÂä†Êó∂,Â∞ÜÊ∑ªÂä†ÁöÑÈ£üÂìÅÊîæÂÖ• stateÁöÑcartFoods[]Êï∞ÁªÑ‰∏≠ Âú®mutatiosÊìç‰Ωú
>2.ÂÖ•stateÁöÑcartFoods[] Êó∂ Âè™Â≠òÂÖ•‰∏Ä‰∏™foodÂØπË±° Âú®ËøõË°å+‰∫ã‰ª∂Êó∂ ‰ªé0->1Êó∂ Â∞ÜfoodÂ≠òÂÖ•cartFoods[]‰∏≠,1->2  2->3 ÈÉΩÊòØÂêå‰∏Ä‰∏™ÂØπË±° Êàë‰ª¨ÈÉΩ‰∏çÈáçÊñ∞Ëé∑ÂèñÂØπË±° ÊâÄ‰ª•ÊîæÂú® +Êó∂ÊâçËøõË°åpushÊï∞ÁªÑ
>3.Âú®ËøõË°å-Êó∂ Âà§Êñ≠countÊòØÂê¶‰∏∫0 Â¶ÇÊûú‰∏∫0 Â∞±Â∞ÜcountÂ±ûÊÄß‰ªé foodÂØπË±°ÁßªÈô§ ‰∏î ‰πü‰ªé cartFoods[] ÁßªÈô§ÂÆÉ
>4.ËøõË°åÂØπÁ±ªÂêçÁöÑÂä®ÊÄÅÂàáÊç¢
>5.Â§ÑÁêÜË¥≠Áâ©ËΩ¶ÂàóË°®ÊòæÁ§∫‰∏éÈöêËóè
>6.ËÆ©Ë¥≠Áâ©ËΩ¶ÂàóË°®ËøõË°åÊªöÂä®  
>>>>>>>>>7.‰ΩøÁî®ÊªöÂä®Êèí‰ª∂Êó∂ÂàáËÆ∞ Áõ∏ÂÖ≥ÂÖÉÁ¥†Âè™ËÉΩÁî® v-show ‰∏çËÉΩÁî®v-if Âê¶ÂàôÂèØËÉΩ‰ºöÂ§±ÊïàÂõ†‰∏∫Êèí‰ª∂Á±ªÂêçÁöÑÂÖ≥Á≥ª
>8.ËøõË°åË¥≠Áâ©ËΩ¶ÁöÑÊ∏ÖÁ©∫ Ë∞ÉÁî®dispatch/commit Ê∏ÖÁ©∫cartFoods

<template>
  <div>
    <div class="shopcart">
      <div class="content">
        <div class="content-left" @click="toggleShow()">
          <div class="logo-wrapper">
            <div class="logo" :class="{ highlight: totalCount > 0 }">
              <i
                class="iconfont iconShoppingcartfinancebusiness "
                :class="{ highlight: totalCount > 0 }"
              ></i>
            </div>
            <div class="num" v-if="totalCount > 0">{{ totalCount }}</div>
          </div>
          <div class="price" :class="{ highlight: totalCount > 0 }">
            Ôø•{{ totalPrice }}
          </div>
          <div class="desc">Âè¶ÈúÄÈÖçÈÄÅË¥πÔø•{{ info.deliveryPrice }}ÂÖÉ</div>
        </div>
        <div class="content-right">
          <div class="pay" :class="payClass">{{ payText }}</div>
        </div>
      </div>
      <transition name="move">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">Ë¥≠Áâ©ËΩ¶</h1>
            <span class="empty" @click="clear">Ê∏ÖÁ©∫</span>
          </div>
          <div class="list-content" ref="foods">
            <ul>
              <li class="food" v-for="food in cartFoods" :key="food.name">
                <span class="name">{{ food.name }}</span>
                <div class="price">
                  <span>Ôø•{{ food.price }}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <CartControl :food="food"></CartControl>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <!-- list-maskÁêÜËß£‰∏∫‰∏Ä‰∏™ËíôÂ±Ç ÁÇπÂáªÂºπÂá∫ËèúÂçïÂàóË°® -->
      <div class="list-mask" v-show="listShow" @click="isShow = !isShow"></div>
    </transition>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import { Dialog } from "vant";
import BScroll from "@better-scroll/core";

export default {
  components: {
    [Dialog.name]: Dialog
  },
  props: {},
  data() {
    return {
      isShow: false //ÊòØÂê¶ÊòæÁ§∫ Â∑≤ÁÇπÂ§ñÂçñÂàóË°®
    };
  },
  created() {},
  mounted() {},
  update() {},
  methods: {
    toggleShow() {
      // Âè™ÊúâÂΩìÊÄªÊï∞Èáè>0ÊâçÂèñÂèç ‰∏∫‰∫ÜÈÅøÂÖçbug
      if (this.totalCount > 0) {
        this.isShow = !this.isShow;
      }
    },
    clear() {
      Dialog.confirm({
        title: "ÊèêÁ§∫",
        message: "Á°ÆËÆ§Ê∏ÖÁ©∫Ë¥≠Áâ©ËΩ¶Âêó?"
      }).then(() => {
        // ËØï‰∏Ä‰∏ã‰∏çÁî®actions
        this.$store.commit("clear_cart");
      });
    }
  },
  computed: {
    ...mapState({
      cartFoods: state => state.shop.cartFoods,
      info: state => state.shop.info //ÂïÜÂÆ∂‰ø°ÊÅØ
    }),
    ...mapGetters(["totalCount", "totalPrice"]),
    payClass() {
      //ÊòæÁ§∫ ËÉåÊôØÈ¢úËâ≤ÂàáÊç¢ ËææÂà∞Ëµ∑ÈÄÅ‰∏∫ ÁªøËâ≤ Âê¶Âàô ‰∏∫ ÁÅ∞Ëâ≤
      const { totalPrice, info } = this; //ÊÄª‰ª∑Ê†º
      const { minPrice } = info; //ÊúÄ‰ΩéËµ∑ÈÄÅ
      if (minPrice && totalPrice) {
        //ÈÅøÂÖçÂà∑Êñ∞Êó∂undefined ÁÑ∂ÂêéÈÄâÈîôÁ±ªÂêç
        return minPrice > totalPrice ? "no-enough" : "enough";
      } else {
        return "no-enough";
      }
    },
    payText() {
      //ÊòæÁ§∫ ËøòÂ∑ÆÂ§öÂ∞ëËµ∑ÈÄÅ ËææÂà∞Ëµ∑ÈÄÅ Âèò‰∏∫ÁªìÁÆó ÊåâÈíÆ
      const { totalPrice, info } = this; //ÊÄª‰ª∑Ê†º
      const { minPrice } = info; //ÊúÄ‰ΩéËµ∑ÈÄÅ
      if (totalPrice === 0) {
        if (minPrice) {
          //ÈÅøÂÖçÂà∑Êñ∞Êó∂undefined
          return `ËøòÂ∑ÆÔø•${minPrice}ÂÖÉËµ∑ÈÄÅ`;
        }
        return `ËøòÂ∑ÆÔø•20ÂÖÉËµ∑ÈÄÅ`;
      } else if (minPrice > totalPrice) {
        return `ËøòÂ∑ÆÔø•${minPrice - totalPrice}ÂÖÉËµ∑ÈÄÅ`;
      } else {
        return "ÂéªÁªìÁÆó";
      }
    },
    listShow() {
      //Ë¥≠Áâ©ËΩ¶ÂàóË°® ÈöêËóèËß¶ÂèëÊù°‰ª∂
      if (this.totalCount === 0) {
        //ÊÄªÊï∞Èáè‰∏∫0Êó∂ ÈöêËóè
        // ‰πüÂ∞±ÊòØ ÊúÄÂêé‰∏Ä‰ª∂ÂïÜÂìÅ‰Ω†ÂéªÊéâ‰πãÂêéÂ∞±‰ºöÈöêËóè
        // ‰∏îÈúÄË¶Åwatch ÁõëÂê¨cartFoods ÈïøÂ∫¶‰∏∫0‰πüËøõË°åÈöêËóè
        return false;
      }
      // Âú®ÊòæÁ§∫Ë¥≠Áâ©ËΩ¶ÂàóË°®ÊâçÂÆû‰æãÂåñÊªöÂä®
      this.$nextTick(() => {
        let self = this;
        if (this.isShow) {
          //‰∏îË¥≠Áâ©ËΩ¶Êó∂ÊòæÁ§∫Áä∂ÊÄÅÊâçËøõË°åÂÆû‰æãÂåñ
          //Âè™Ë∞ÉÁî®‰∏ÄÊ¨° ÈÅøÂÖçÂ§öÊ¨°Ë∞ÉÁî®
          if (!this.scroll) {
            self.scroll = new BScroll(this.$refs.foods, {
              click: true //ÈªòËÆ§ÈòªÊ≠¢‰∫ÜÁÇπÂáª‰∫ã‰ª∂ Êàë‰ª¨Â∞ÜÂÆÉÂºÄÂêØ
            });
          } else {
            this.scroll.refresh(); //*Âà∑Êñ∞ÊªöÂä®ÂØπË±°Âà∑Êñ∞ ÈáçÊñ∞ËÆ°ÁÆóÊòØÂê¶ÈúÄË¶ÅÊªëÂä®
          }
        }
      });
      return this.isShow;
    }
  },
  watch: {
    cartFoods() {
      // ÂΩìÂïÜÂìÅÂàóË°®ÈïøÂ∫¶‰∏∫0Êó∂ ËøõË°åÈöêËóè
      if (this.cartFoods.length === 0) {
        this.isShow = false;
      }
    }
  }
};
</script>

