 1.ÂÆûÁé∞ÊªöÂä®
 2.ÂÆûÁé∞ÂïÜÂìÅÂàóË°®ÊªöÂä®ÊòØ Â∑¶‰æßÂàÜÁ±ªÊªöÂä®Âà∞ÂØπÂ∫îÁöÑÂàÜÁ±ª
 3.ÂÆûÁé∞ÁÇπÂáªÂàÜÁ±ª ÂïÜÂìÅÂàóË°®ÊªöÂä®Âà∞ÂØπÂ∫îÂïÜÂìÅ

ÂºÄÂßã:1.ËÆæËÆ°ÂΩìÂâçÂàÜÁ±ª‰∏ãË°® currentIndex === Âæ™ÁéØÁöÑindex  ÊªöÂä®Êù•Á°ÆÂÆöcurrentIndex(ËÆ°ÁÆóÂ±ûÊÄß)
2.ÂÆûÁé∞ÊªöÂä®‰∫ã‰ª∂ 
---- scrollYÂè≥‰æßÊªöÂä®yÂùêÊ†á  ÂàùÂßã‰∏∫0  ÊªöÂä®ÂÆûÊó∂ÊîπÂèò
---- topsÊï∞ÁªÑÂ≠òÊîæÂïÜÂìÅÂàóË°®ÁöÑÊØè‰∏™ÂàÜÁ±ª(li) È´òÂ∫¶ÁöÑÊï∞ÁªÑ Âõ∫ÂÆö‰∏çÂèò
tops[0,5,8,12]  [top,nextTop)   currentIndex=2  8-11ÈÉΩ‰∏∫2 12‰∏∫3
ÂèØ‰ª•ÂæóÂá∫üëá
current= currentIndex===index
-1-currentIndex=tops.findIndex((top,index)=>{scrollY>=top && scrollY<top[index+1]})
Êù•Á°ÆÂÆöÁ±ªÂêçcurrentÊòØÂê¶ÈÄâ‰∏≠
-2-ËøõË°åÊªöÂä®‰∫ã‰ª∂ÁõëÂê¨ scrollÂ∫ìÁöÑscrollÂíåscrollEnd‰∫ã‰ª∂ÁõëÂê¨ ÂÆûÊó∂Ë∑üÊñ∞scrollY‰ªéËÄåÈÄâ‰∏≠Âè≥‰æßÁ±ªÂêç
-3-ÁÇπÂáªÂ∑¶‰æßÊåâÈíÆ Âè≥‰æßËèúÂçïÊªëÂä®Âà∞Âà∂ÂÆö‰∏∫‰ΩçÁΩÆ ÁªëÂÆöÁÇπÂáª‰∫ã‰ª∂‰º†ÂÖ•index Ê†πÊçÆÁÇπÂáªÁöÑindex ÈÄâ‰∏≠topsÊï∞ÁªÑ ËøõË°å-ÁßªÂä®
-4-ÊªëÂä®Âè≥‰æßÂàóË°®ÂùêËΩ¶ÈúÄË¶ÅÊòæÁ§∫Âú®ÂèØËßÜËåÉÂõ¥ÂÜÖ ËÄå‰∏çÊòØÊàë‰ª¨ÊâãÂä®Êªë‰∏ãÂéªÊâçÁúãÁöÑÂà∞, Êàë‰ª¨Â∞ÜÂ∑¶‰æßÈÄâ‰∏≠ÁöÑ‰∏ÄÁõ¥‰∏∫ÊúÄÈ°∂ÈÉ®ÊòæÁ§∫,‰∏ãÈù¢Á©∫Èó¥‰∏çÂ§ü‰∫ÜÊâçÂæÄ‰∏ãÈÄâ‰∏≠
--ÊâçËÆ°ÁÆócurrentIndexÊó∂ ‰ΩøÁî®scrollToElement ÊªöÂä®Âà∞ÁõÆÊ†áÂÖÉÁ¥† Êàë‰ª¨ÈÄöËøáÂà§Êñ≠ÁßªÂä®Êó∂ ‰∏ä‰∏Ä‰∏™indexÊòØÂê¶Âíåindex‰∏çÂêå,‰∏çÂêåÂ∞±ÊâßË°åËøô‰∏™ÊñπÊ≥ïÂç≥ÂèØ
-5-ÂÆåÊØïÊé•‰∏ãÊù•ÂÆåÊàê CartControl ÁªÑ‰ª∂ ÊåâÈíÆÁªÑ‰ª∂ ‰∏∫ÂÖ®Â±ÄÁªÑ‰ª∂
<template >
  <div class="goods" >
    <div class="menu-wrapper" ref="left">
      <ul ref='leftUL'>
        <li class="menu-item" 
        :class="{current:currentIndex===index}" 
        @click='selectItem(index)'
        v-for="(good,index) in goods" :key="good.name">
          <span class="text bottom-border-1px">
            <img class="icon" v-if="good.icon" v-lazy="good.icon" />
          {{good.name}}
            </span
          >
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="right">
      <ul ref='rightUL'>
        <li class="food-list-hook" v-for="good in goods" :key="good.name">
          <h1 class="title">{{ good.name }}</h1>
          <ul>
            <li
              class="food-item bottom-border-1px"
              v-for="food in good.foods"
              :key="food.name"
              @click="showFood(food)"
            >
              <div class="icon">
                <img width="57" height="57" v-lazy="food.icon" />
              </div>
              <div class="content">
                <h2 class="name">{{ food.name }}</h2>
                <p class="desc">{{ food.description }}</p>
                <div class="extra">
                  <span class="count">ÊúàÂîÆ{{ food.sellCount }}‰ªΩ</span>
                  <span>Â•ΩËØÑÁéá{{ food.rating }}%</span>
                </div>
                <div class="price">
                  <span class="now">Ôø•{{ food.price }}</span>
                  <span class="old" v-if="food.oldPrice"
                    >Ôø•{{ food.oldPrice }}</span
                  >
                </div>
                <div class="cartcontrol-wrapper">
                  <CartControl :food='food'></CartControl>
                </div>
              </div>
            </li>

          </ul>
        </li>
      </ul>
    </div>
    <ShopCart></ShopCart>
  <FoodImage ref='foodImg' :food="food"></FoodImage>
  </div>
</template>

<script>
import { mapState } from "vuex";
import BScroll from "@better-scroll/core";
import ShopCart from "../../../components/ShopCart/ShopCart";
import FoodImage from "../../../components/FoodImage/FoodImage";

export default {
  components: {
    ShopCart,
    FoodImage
  },
  props: {},
  data() {
    return {
      scrollY: 0,
      tops: [], //ÊâÄÊúâliÁöÑÈ´òÂ∫¶
      food: {} //ÁÇπÂáªÊó∂ÁöÑÂΩìÂâçfood
    };
  },
  created() {},
  mounted() {
    this.$store.dispatch("getGoods", () => {
      this.$nextTick(() => {
        this._initScroll(); //ËøõË°åÊªöÂä®ÂàùÂßãÂåñ   ËøôÈáåËøõË°åÂØπleft rightÊªöÂä®ÂàùÂßãÂåñ
        this._initTops(); //ËøõË°åÂØπÊØè‰∏™È£üÂìÅliËÆ°ÁÆó
      });
    });
  },
  update() {},
  methods: {
    // ÈÄâ‰∏≠liÊï∞ÁªÑ ÂàùÂßãÂåñ
    _initTops() {
      const tops = []; //liÈ´òÂ∫¶Êï∞ÁªÑ
      let top = 0; //Á¥ØÂä†ÁöÑliÈ´òÂ∫¶
      tops.push(top);
      // Array.of()
      const lis = [...this.$refs.rightUL.children]; //‰º™Êï∞ÁªÑÊù•ÁöÑ ...Âèò‰∏∫ÁúüÊï∞ÁªÑ
      lis.forEach((lis, li) => {
        top += lis.clientHeight; //Ëé∑ÂèñÊØè‰∏™liÂèØËßÜÈ´òÂ∫¶
        tops.push(top);
      });
      this.tops = tops;
    },
    // ÊªöÂä®ÂÆû‰æã ÂàùÂßãÂåñ
    _initScroll() {
      this.leftScroll = new BScroll(this.$refs.left, {
        click: true //ÈªòËÆ§ÈòªÊ≠¢‰∫ÜÁÇπÂáª‰∫ã‰ª∂ Êàë‰ª¨Â∞ÜÂÆÉÂºÄÂêØ
      });
      this.rightScroll = new BScroll(this.$refs.right, {
        click: true, //ÈªòËÆ§ÈòªÊ≠¢‰∫ÜÁÇπÂáª‰∫ã‰ª∂ Êàë‰ª¨Â∞ÜÂÆÉÂºÄÂêØ
        probeType: 1 //ÂÖÅËÆ∏ÂºÄÂêØscrollÂÆûÊñΩÁõëÂê¨
        // 1‰∏∫ ÈùûÂÆûÊó∂ÁõëÂê¨  ‰∏∫Êåâ‰ΩèÊó∂Ëß¶Êë∏ÊâçËß¶Âèë  Á®çÂæÆËäÇÊµÅÊÄßËÉΩÂ•Ω
        // 2‰∏∫ÂÆûÊó∂ ÁõëÂê¨ÊªöÂä®Ëß¶Êë∏
        // 3‰∏∫ÂÆûÊó∂ÁõëÂê¨ÊªöÂä®Ëß¶Êë∏ + ÁõëÂê¨ÊÉØÊÄßÊªëÂä® + ÁºñÁ†ÅÁßªÂä®
      });
      // ÁªôrightScrollÁªëÂÆöscrollÁõëÂê¨
      this.rightScroll.on("scroll", ({ x, y }) => {
        this.scrollY = Math.abs(y); //*Ëé∑ÂèñÂè≥‰æßÊªöÂä®ÁöÑÂùêÊ†á
      });
      // ÊªöÂä®ÁªìÊùüÊó∂
      this.rightScroll.on("scrollEnd", ({ x, y }) => {
        this.scrollY = Math.abs(y);
      });
    },
    // ~Â∑¶‰æßÈÄâ‰∏≠ÂàÜÁ±ª Âè≥‰æßliÊªöÂä®Âà∞ÊåáÂÆöÂïÜÂìÅÂàóË°®
    selectItem(index) {
      const y = this.tops[index];
      // Á´ãÂç≥Êõ¥Êñ∞scrollY ËÆ©Â∑¶‰æßÁ±ªÂêçÈÄâ‰∏≠ Ëß£ÂÜ≥Âª∂ËøüÈóÆÈ¢ò
      this.scrollY = y;
      //Ê†πÊçÆÁÇπÂáªÁöÑindex ËÆ©Âè≥‰æßÁºñÁ†ÅÊªëÂä®Âà∞ÊåáÂÆö‰ΩçÁΩÆ Ê≥®ÊÑèÊó∂ - ÁöÑ
      this.rightScroll.scrollTo(0, -y, 500);
    },
    // Ë¢´ÁÇπÂáªÊó∂  ÊòæÁ§∫ÊåáÂÆöfoodÁöÑÂõæÁâáÈ¢ÑËßà
    showFood(food) {
      const img = this.$refs.foodImg;
      img.toggleShow(); //Ëß¶ÂèëÂ≠êÁªÑ‰ª∂ÁöÑ foodÈöêËóèÊòæÁ§∫‰∫ã‰ª∂
      this.food = food; //‰º†ÂÖ•ÁÇπÂáªÁöÑfood
    }
  },
  computed: {
    ...mapState({
      goods: state => state.shop.goods
    }),
    //*currentIndex‰ΩúÁî®:Âè≥‰æßliÊªöÂä®Âà∞ÊåáÂÆö‰ΩçÁΩÆÊó∂Â∑¶‰æßÈÄâ‰∏≠ÊåáÂÆöÊ†∑Âºè
    // ÂΩìÂâçÂàÜÁ±ª‰∏ãÊ†á
    currentIndex() {
      const { scrollY, tops } = this; //Ëé∑ÂèñÂçïÂâçÂè≥‰æßÊªöÂä®‰ΩçÁΩÆ ‰ª•Âèä li‰ΩçÁΩÆÊï∞ÁªÑ
      // ËÆ°ÁÆóÂæóÂà∞Êñ∞ÁöÑ‰∏ãÊ†á
      const index = tops.findIndex(
        //*ËøîÂõûÂçïÂâçÊªöÂä®‰ΩçÁΩÆ Â§ß‰∫éÊüê‰∏ÄÈ°π‰∏îÂ∞è‰∫éÊüê‰∏ÄÈ°πÁöÑ‰ΩçÁΩÆ
        (top, index) => scrollY >= top && scrollY < tops[index + 1] //ÂΩìÂâçliÊªöÂä®ËåÉÂõ¥ÁöÑindex
      );
      // ÈúÄË¶ÅÂà§Êñ≠leftScrollÊòØÂê¶ÊúâÊúâÂõ†‰∏∫Á¨¨‰∏ÄÊ¨°Êï∞ÊçÆÊ∏≤Êüì ‰ºöÂª∂Ëøü
      if (this.leftScroll) {
        //ÈúÄË¶ÅÂàùÂßãÂåñÂêéÊâçÂÖÅËÆ∏‰Ω†ÊªöÂä®
        //  ËÆ©Â∑¶‰æßÂàóË°®ÊªëÂä®Âà∞ÂØπÂ∫îÁöÑli
        const li = this.$refs.leftUL.children[index]; //Ëé∑ÂæóÂè≥‰æßÂàóË°®ÁöÑindex,ÁÑ∂ÂêéÁî®ÂÖ∂Ëé∑Âèñleft liÊ†áÁ≠æ
        this.leftScroll.scrollToElement(li, 200); //ËÆ©Â∑¶‰æßÂàóË°®ÊªöÂä®Âà∞liÊåáÂÆö‰ΩçÁΩÆ
      }
      return index; //ËøîÂõûÂè≥‰æßÊªöÂä®Âà∞‰∫ÜÂì™‰∏™liÁöÑindex ËÆ©Â∑¶‰æßÁöÑÁ±ªÂêç‰πüÈÄâ‰∏≠
    }
  },
  watch: {
    goods(){
      console.log(123)
    }
  }
};
</script>

<style lang="stylus"  rel="stylesheet/stylus" scoped>
